.about-container{
    height: 84%;
    width: 100%;
    z-index: 3;
    transition: 1s ease;    
}

.about-content{
    position: absolute;
    width: 80%;
    height: 80%;
    left: 50%;
    transform: translate(-50%);
    padding: 10px;
    z-index: 2;

}

.about-content svg{
    position: relative;
    width: 90%;
}
.about-content p{
    margin-top: -35px;
}
#person-svg{
    width: 60%;
    position: absolute;
    right: 0%;
}

.animation-container{
    width: 90%;
    height: 50%;
    position: relative;
    left: 50%;
    transform: translate(-50%);
}