@font-face {
    src: url(https://fonts.googleapis.com/css2?family=M+PLUS+1p&family=M+PLUS+Rounded+1c:wght@700&family=Noto+Sans+JP&display=swap);
    font-family: 'M PLUS 1p', sans-serif;
  }
.layout{
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}
#svg-blob{
    position: absolute;
    bottom: 0px;
    width: 100%;
    z-index: 1;
    
}
 .header{
     height: 20px;
 }
.header ul{
    position: relative;
    top: -120px;
    display: flex;
    flex-direction: row;
    background: rgb(255, 255, 255, 0.14);
   
    font-weight: 700;
    font-size: 15px;
    color: #fff;
    padding-right: 0px;
    padding-left: 0px;
}

.header ul li{
    width: 25%;
    padding: 5px;
    list-style: none;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    justify-content: center;
    transition: .1s linear;
}
.header ul li:active{
    opacity: 0.7;

}
#bg-div{
    position: sticky;
    top: 0px;
    height: 100px;
}
#bg-text{
    position: relative;
    font-weight: 900;
    font-size: 192px;
    top: -200px;
    right: -50px;

}
