html,
body {
  padding: 0;
  margin: 0;
  font-family: 'M PLUS Rounded 1c', sans-serif;
}
@font-face {
  font-family: 'M PLUS Rounded 1c', sans-serif;
  src: url(https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c:wght@700&family=Noto+Sans+JP&display=swap);
}
a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

