.feed{
    width: 100%;
    height: 100%;
    transition: 1s ease;    

}

.feed-container{
    position: relative;
    top: 50px;
    left: 50%;
    transform: translate(-50%);
    max-width: 350px;
    height: 400px;
}


#dot-active{
    height: 13px;
    width: 13px;
    background-color: rgb(0, 255, 13);
    border-radius: 50%;
    transition: .5s ease;
}


.card-element img{
    border: 1px solid red;
    width: 100%;
    height: 70%;
    border-radius: 5;
    align-self: flex-start;
}

.overlay-body p span{
    color: black;
    font-weight: 700;
}

.overlay-body p{
    font-size: 14px;
}

.overlay-body {
    padding: 15px;
}

.card-body{
    background: #F8F7F7;
    width: 100%;
    height: 400px;
    border-radius: 5px;
    display: block;
    justify-content: center;
    
    display: flex;
    justify-content: center;
    align-items: flex-start;
   
    box-shadow: 2px 4px 15px rgba(0, 0, 0, 0.25);
}
.blob{
    position: absolute;
    align-self: flex-end;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    height: 100%;
    z-index: 1;
}
.card-element{

    position: absolute;
    z-index: 2;
    height: 80%;

    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.overlay {
    position: relative;
    height: 10%;
    width: 40%;
    border-radius: 30px;
    transition: .5s ease;

    margin-bottom: 10px;

    color: white;
    align-self: flex-end;

    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 3;
    cursor: pointer;
  }

  
  .overlay-active {
    border-radius: 5px;
    margin: 0px;
    height: 100%;
    width: 100%;
    align-self: flex-end;
    transition: .5s ease;

    text-align: center;
    color: white;
    z-index: 2;
  }
   
#dot{
    height: 12px;
    width: 12px;
    background-color: #bbb;
    border-radius: 50%;
    transition: .5s ease;
}

#dot-nav{
    position: relative;
    left: 50%;
    transform: translate(-50%);
    margin-top: 30px;
    width: 40%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    
}

.link-animation{
    position: relative;
    left: 50%;
    transform: translate(-50%);
    width: 100px;
    height: 100px;
}